"use client"

import { useState } from "react"
import { useRouter } from "next/navigation"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Alert, AlertDescription } from "@/components/ui/alert"
import { Eye, EyeOff, LogIn, Package, Shield, Sparkles } from "lucide-react"
import Link from "next/link"
import BackgroundEffects from "@/components/BackgroundEffects"

export default function LoginPage() {
  const [email, setEmail] = useState("")
  const [password, setPassword] = useState("")
  const [showPassword, setShowPassword] = useState(false)
  const [loading, setLoading] = useState(false)
  const [error, setError] = useState("")
  const router = useRouter()

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    setLoading(true)
    setError("")

    try {
      const response = await fetch("/api/auth/login", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ email, password })
      })

      const data = await response.json()

      if (response.ok) {
        localStorage.setItem("user", JSON.stringify(data.user))
        localStorage.setItem("userId", data.user.id)
        router.push("/dashboard")
      } else {
        setError(data.error || "Login failed")
      }
    } catch (error) {
      setError("Network error. Please try again.")
    } finally {
      setLoading(false)
    }
  }

  return (
    <div className="min-h-screen flex items-center justify-center relative overflow-hidden bg-gradient-to-br from-slate-900 via-purple-900 to-slate-900 p-4">
      <BackgroundEffects />
      <div className="absolute inset-0 bg-grid opacity-20" />
      <div className="floating-shapes absolute inset-0" />
      <div className="w-full max-w-md relative z-10">
        <div className="text-center mb-8">
          <div className="inline-flex items-center justify-center w-20 h-20 glass rounded-3xl mb-6 pulse-glow">
            <Package className="w-10 h-10 text-white" />
          </div>
          <h1 className="text-4xl font-bold gradient-text mb-2">
            StockMaster
          </h1>
          <p className="text-white/70 text-lg">Enterprise Inventory Management</p>
          <div className="flex items-center justify-center gap-2 mt-4">
            <div className="w-2 h-2 bg-green-400 rounded-full animate-pulse" />
            <span className="text-white/60 text-sm">System Online</span>
          </div>
        </div>

        <Card className="border-0 shadow-2xl glass backdrop-blur-xl">
          <CardHeader className="text-center pb-6">
            <CardTitle className="text-xl font-semibold flex items-center justify-center gap-2 text-white">
              <Shield className="w-5 h-5 text-blue-400" />
              Secure Access Portal
            </CardTitle>
            <p className="text-white/60 text-sm">Enter your credentials to continue</p>
          </CardHeader>
          <CardContent className="space-y-6">
            <form onSubmit={handleSubmit} className="space-y-4">
              {error && (
                <Alert variant="destructive" className="border-destructive/20">
                  <AlertDescription>{error}</AlertDescription>
                </Alert>
              )}
              
              <div className="space-y-2">
                <Label htmlFor="email" className="text-sm font-medium text-white/80">Email Address</Label>
                <Input
                  id="email"
                  type="email"
                  value={email}
                  onChange={(e) => setEmail(e.target.value)}
                  required
                  placeholder="Enter your email"
                  className="h-12 glass border-white/20 text-white placeholder:text-white/50 focus:border-blue-400 transition-all duration-300"
                />
              </div>
              
              <div className="space-y-2">
                <Label htmlFor="password" className="text-sm font-medium text-white/80">Password</Label>
                <div className="relative">
                  <Input
                    id="password"
                    type={showPassword ? "text" : "password"}
                    value={password}
                    onChange={(e) => setPassword(e.target.value)}
                    required
                    placeholder="Enter your password"
                    className="h-12 glass border-white/20 text-white placeholder:text-white/50 focus:border-blue-400 transition-all duration-300 pr-12"
                  />
                  <button
                    type="button"
                    onClick={() => setShowPassword(!showPassword)}
                    className="absolute right-3 top-1/2 -translate-y-1/2 text-white/60 hover:text-white transition-colors"
                  >
                    {showPassword ? <EyeOff className="w-5 h-5" /> : <Eye className="w-5 h-5" />}
                  </button>
                </div>
              </div>
              
              <Button 
                type="submit" 
                className="w-full h-12 bg-gradient-to-r from-blue-600 via-purple-600 to-blue-600 hover:from-blue-500 hover:via-purple-500 hover:to-blue-500 shadow-2xl border-0 text-white font-semibold transition-all duration-300 transform hover:scale-[1.02] pulse-glow" 
                disabled={loading}
              >
                {loading ? (
                  <div className="flex items-center gap-2">
                    <div className="w-5 h-5 border-2 border-white/30 border-t-white rounded-full animate-spin" />
                    Authenticating...
                  </div>
                ) : (
                  <div className="flex items-center gap-2">
                    <LogIn className="w-5 h-5" />
                    Access Dashboard
                  </div>
                )}
              </Button>
            </form>
            
            <div className="text-center space-y-3">
              <p className="text-sm text-white/60">
                Don't have an account?{" "}
                <Link href="/register" className="text-blue-400 hover:text-blue-300 font-medium transition-colors">
                  Sign up
                </Link>
              </p>
              <p className="text-sm">
                <Link href="/reset-password" className="text-purple-400 hover:text-purple-300 font-medium transition-colors">
                  Forgot your password?
                </Link>
              </p>
            </div>
            
            <div className="glass rounded-xl p-4 border border-white/10">
              <div className="flex items-center gap-2 mb-3">
                <Sparkles className="w-4 h-4 text-yellow-400" />
                <p className="text-sm font-medium text-white">Demo Accounts</p>
              </div>
              <div className="space-y-2 text-xs">
                <div className="flex justify-between items-center p-2 glass rounded-lg border border-white/10">
                  <span className="font-medium text-white">Admin</span>
                  <span className="text-white/60">admin@example.com</span>
                </div>
                <div className="flex justify-between items-center p-2 glass rounded-lg border border-white/10">
                  <span className="font-medium text-white">Manager</span>
                  <span className="text-white/60">manager@example.com</span>
                </div>
                <div className="flex justify-between items-center p-2 glass rounded-lg border border-white/10">
                  <span className="font-medium text-white">Staff</span>
                  <span className="text-white/60">staff@example.com</span>
                </div>
                <p className="text-center text-white/60 mt-3">Password: <code className="glass px-2 py-1 rounded text-white border border-white/20">password123</code></p>
              </div>
            </div>
          </CardContent>
        </Card>
        
        <p className="text-center text-xs text-white/40 mt-8">
          Â© 2024 StockMaster. Enterprise-grade inventory management.
        </p>
        <div className="flex justify-center gap-4 mt-4">
          <div className="w-8 h-1 bg-gradient-to-r from-blue-400 to-transparent rounded-full" />
          <div className="w-8 h-1 bg-gradient-to-r from-purple-400 to-transparent rounded-full" />
          <div className="w-8 h-1 bg-gradient-to-r from-pink-400 to-transparent rounded-full" />
        </div>
      </div>
    </div>
  )
}
